import * as React from 'react';
import {Container, Grid, Header, Segment} from "semantic-ui-react";
import ContactCard from "./components/ContactCard";
import {person} from "./person";

import profileImg from './profile-lg.jpg';
import WorkExperienceCard from "./components/WorkExperienceCard";
import EducationCard from "./components/EducationCard";
import SkillCard from "./components/SkillCard";

interface SiteProps {
}

interface SiteState {
    jsonSchema: any;
}

class App extends React.Component<SiteProps, SiteState> {
    constructor(props: SiteProps, state: SiteState) {
        super(props, state);

        this.state = {
            jsonSchema: person,
        }
    };

    public render() {
        const workExperienceSection = this.state.jsonSchema.work.map( (workExperience: any) => (<WorkExperienceCard
            company={workExperience.company}
            position={workExperience.position}
            website={workExperience.website}
            startDate={workExperience.startDate}
            endDate={workExperience.endDate}
            summary={workExperience.summary}
            highlights={workExperience.highlights}/>));

        const educationSection = this.state.jsonSchema.education.map( (education: any) => (<EducationCard
            institution={education.institution}
            area={education.area}
            studyType={education.studyType}
            startDate={education.startDate}
            endDate={education.endDate}
            gpa={education.gpa}
            courses={education.courses}/>));

        const skillSection = this.state.jsonSchema.skills.map( (skill: any) => (<SkillCard
            name={skill.name}
            keywords={skill.keywords}/>));

        return (
          <Container>
            <Grid>
              <Grid.Row>
                  <Grid.Column computer={4}>
                      <ContactCard profileImg={profileImg} basics={this.state.jsonSchema.basics}/>
                      <Segment>
                          <Header>
                              Skills
                          </Header>
                          {skillSection}
                      </Segment>
                  </Grid.Column>
                  <Grid.Column computer={12}>
                      <Segment>
                          <Header as={'h2'} >
                              Work Experience
                          </Header>
                          {workExperienceSection}
                      </Segment>
                      <Segment>
                          <Header as={'h2'} >
                              Education
                          </Header>
                          {educationSection}
                      </Segment>
                  </Grid.Column>
              </Grid.Row>
            </Grid>
          </Container>
        );
    }
}

export default App;



