import * as React from 'react';
import {Card, Image, List} from "semantic-ui-react";

interface Profile {
    network: string;
    username: string;
    url: string;
}

interface Props {
    basics: any;
    profileImg: any;
}

interface State {

}

class ContactCard extends React.Component<Props, State> {
    constructor(props: Props, state: State) {
        super(props, state);

    }

    public render() {
        const profiles = this.props.basics.profiles.map((profile: Profile, index: number) => (
            <List.Item>
                <List.Icon name='linkify' />
                <List.Content>
                    <a key={index} href={profile.url}>{profile.network} | {profile.username}</a>
                </List.Content>
            </List.Item>
        ));

        return (
            <Card fluid={true}>
                <Image src={this.props.profileImg} circular={true} />
                <Card.Content>
                    <Card.Header>{this.props.basics.name}</Card.Header>
                    <Card.Meta>{this.props.basics.label}</Card.Meta>
                    <Card.Description>{this.props.basics.summary}</Card.Description>
                </Card.Content>
                <Card.Content extra={true}>
                    <List>
                        <List.Item>
                            <List.Icon name={'mail'} />
                            <List.Content>
                                <a href={"mailto:" + this.props.basics.email}>{this.props.basics.email}</a>
                            </List.Content>
                        </List.Item>
                        <List.Item>
                            <List.Icon name={'phone'} />
                            <List.Content>
                                <a href={"tel:" + this.props.basics.phone}>{this.props.basics.phone}</a>
                            </List.Content>
                        </List.Item>
                        {profiles}
                    </List>
                </Card.Content>
            </Card>
        );
    }
}

export default ContactCard;


