import * as React from "react";
import {Header, Label, Segment} from "semantic-ui-react";

/*
{
            name: 'Java',
            level: '80',
            keywords: [
                'Spring',
                'Java EE',
            ],
        }
 */

interface Props {
    name: string;
    level?: string;
    keywords: string[];
}

interface State {
}

class SkillCard extends React.Component<Props, State> {

    constructor(props: Props, state: State) {
        super(props, state);
    }

    public render() {
        const keywords = this.props.keywords.map( keyword => (<Label tag={true} size={'small'}>{keyword}</Label> ));
        return (
            <Segment.Group raised={true}>
                <Segment>
                    <Header as={'h4'}>{this.props.name}</Header>
                    {keywords}
                </Segment>
            </Segment.Group>
        );
    }
}

export default SkillCard;


