import * as React from "react";
import {Header, Label, Segment} from "semantic-ui-react";

interface Props {
    company: string;
    position: string;
    website: string;
    startDate: string;
    endDate: string;
    summary: string;
    highlights: string[];
}

interface State {

}

class WorkExperienceCard extends React.Component<Props, State> {

    constructor(props: Props, state: State) {
        super(props, state);
    }

    public render() {
        const highlights = this.props.highlights.map( highlight => (<Label tag={true} color={'blue'} size={'small'}>{highlight}</Label> ));
        return (
            <Segment.Group raised={true}>
                <Segment>
                    <Header><a href={this.props.website}>{this.props.company}</a></Header>
                    {highlights}
                </Segment>
                <Segment>
                    {this.props.position}
                </Segment>
                <Segment>
                    {this.props.startDate} - {this.props.endDate}
                </Segment>
                <Segment>
                    {this.props.summary}
                </Segment>
            </Segment.Group>
        );
    }
}

export default WorkExperienceCard;


