import * as React from "react";
import {Header, Label, Segment} from "semantic-ui-react";

/*
{
            institution: 'University of Central Arkansas',
            area: 'GIS/MIS',
            studyType: 'B.A. in Geography',
            startDate: '2010-08',
            endDate: '2012-05',
            gpa: '3.6',
            courses: [
                'GIS',
                'Java',
                'Web Development',
                'System Design',
                'Database',
                'Statistics'
            ],
        }
 */

interface Props {
    institution: string;
    area: string;
    studyType: string;
    startDate: string;
    endDate: string;
    gpa: string;
    courses: string[];
}

interface State {

}

class EducationCard extends React.Component<Props, State> {

    constructor(props: Props, state: State) {
        super(props, state);
    }

    public render() {
        const courses = this.props.courses.map( course => (<Label tag={true} color={'teal'} size={'small'}>{course}</Label> ));
        return (
            <Segment.Group raised={true}>
                <Segment>
                    <Header>{this.props.institution}</Header>
                    {courses}
                </Segment>
                <Segment>
                    {this.props.area}
                </Segment>
                <Segment>
                    {this.props.startDate} - {this.props.endDate}
                </Segment>
                <Segment>
                    Graduated with a {this.props.studyType}
                </Segment>
            </Segment.Group>
        );
    }
}

export default EducationCard;


